@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
/*Auth*/
.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
}

/*AuthForm*/
.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

/*TwitFactory*/
.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 300px;
}

.factoryInput_input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500px;
  font-size: 12px;
}

.factoryInput_arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput_label {
  color: #04aaff;
  cursor: pointer;
}

.factoryInput_label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm_attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm_attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factoryForm_clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm_clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Twit */

.twitEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.twit {
  margin-bottom: 40px;
  background-color: white;
  width: 320px;
  max-width: 320px;
  padding: 25px 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.twit h4 {
  font-size: 14px;
}

.twit img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.twit_actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.twit_actions span {
  cursor: pointer;
}

.twit_actions span:first-child {
  margin-right: 10px;
}

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

.nickNameForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 220px;
  display: flex;
  flex-direction: column;
}

.nickNameInput {
  width: 220px;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.nickNameBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
}

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
          appearance: none;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.twit-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.twit-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.twit-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 320px;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

